<template>
  <div id="container" ref="cont">
    <div id="toolbar">
      <div id="toolbar-document-buttons">
        <button v-on:click="saveChanges">Save</button>
        <button v-on:click="exitEditor">Exit editor</button>
        <div id="editor-title">
          <h3>GitBuilding Live Editor</h3>
        </div>
      </div>
    </div>
    <div id="include_select_bar" v-if="includeOptions">
      <strong>This page's content is embedded in other pages.</strong> Render
      this page
      <select class="form-control" @change="updateInclude($event)">
        <option value="" selected>on its own</option>
        <option v-for="include in includeList" :value="include" :key="include"
          >within {{ include }}</option
        >
      </select>
    </div>
    <div id="duplicate_warning_bar">
      <div id="duplicate_warning" v-html="duplicateWarning"></div>
    </div>
    <div id="editor">
      <div id="editor-panel" v-bind:style="{ width: editor_width }">
        <div id="toolbar-text-buttons">
          <button id="bold-button" v-on:click="addBold">B</button>
          <button id="italic-button" v-on:click="addItalic">I</button>
          <button id="link-button" v-on:click="addLink">Link</button>
          <div class="vertLine"></div>
          <button id="bom-button" v-on:click="addBom">BOM</button>
          <button id="part-button" v-on:click="addPart">Part</button>
        </div>
        <textarea
          ref="edtext"
          v-bind:value="editor_text"
          v-on:input="updateFromEditor"
        ></textarea>
      </div>
      <div id="dragbar" ref="dragbar"></div>
      <div
        id="preview-panel"
        v-bind:style="{
          width: preview_width,
          'grid-template-rows': preview_rows
        }"
      >
        <div id="preview-window" v-html="compiledMarkdown"></div>
        <div
          id="warning-counter"
          ref="warningcounter"
          v-bind:style="{ background: warningColour }"
        >
          {{ warningNumber }} {{ warningShowMsg }}
        </div>
        <div id="warning-window" v-html="compilerLog"></div>
      </div>
    </div>
    <div
      id="saving-background"
      v-bind:style="{ visibility: saveMessageVisible }"
    >
      <div id="saving-message">
        Please wait while project rebuilds
        <vue-simple-spinner size="medium" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Editor",

  components: {},

  data() {
    return {
      saving: false,
      saved_text: "",
      editor_text: "",
      editor_text_debounced: "",
      duplicated_by: [],
      included_by: [],
      show_within: "",
      page: "",
      compilerOutput: new Promise(function(resolve) {
        resolve({ data: { html: "", number: 0, log: "" } });
      }),
      dragAndDropCapable: false,
      files: [],
      uploadedFiles: [],
      editor_width: "49.75%",
      preview_width: "49.75%",
      preview_rows: "auto 24px 0",
      warnings_visible: false
    };
  },

  mounted() {
    window.addEventListener("keydown", event => {
      if (event.ctrlKey || event.metaKey) {
        switch (String.fromCharCode(event.which).toLowerCase()) {
          case "s":
            event.preventDefault();
            this.saveChanges();
            break;
        }
      }
    });
    window.addEventListener("beforeunload", this.checkSave);
    this.$refs.dragbar.addEventListener("mousedown", this.enable_dragbar);

    this.$refs.warningcounter.addEventListener("click", this.hideshow_warnigns);

    this.dragAndDropCapable = this.determineDragAndDropCapable();

    if (this.dragAndDropCapable) {
      //Listen to all of the drag events and bind an event listener to each for the editor textbox.
      [
        "drag",
        "dragstart",
        "dragend",
        "dragover",
        "dragenter",
        "dragleave",
        "drop"
      ].forEach(
        function(evt) {
          /* For each event add an event listener that prevents the default action (opening the file in the browser)
        and stop the propagation of the event (so no other elements open the file in the browser)*/
          this.$refs.edtext.addEventListener(
            evt,
            function(e) {
              e.preventDefault();
              e.stopPropagation();
            }.bind(this),
            false
          );
        }.bind(this)
      );

      //Add an event listener for files dropped into editor
      this.$refs.edtext.addEventListener(
        "drop",
        function(e) {
          // Capture the files from the drop event and submit them
          for (let i = 0; i < e.dataTransfer.files.length; i++) {
            this.files.push(e.dataTransfer.files[i]);
          }
          this.submitFiles();
        }.bind(this)
      );
    }
  },

  watch: {
    watched_content: function() {
      this.compilerOutput = this.$http.post("/-/render_markdown", {
        md: this.editor_text_debounced,
        page: this.page,
        show_within: this.show_within
      });
    }
  },

  asyncComputed: {
    watched_content() {
      return { md: this.editor_text_debounced, show_within: this.show_within };
    },
    compiledMarkdown() {
      return this.compilerOutput.then(r => r.data.html);
    },
    compilerLog() {
      return this.compilerOutput.then(r => r.data.log);
    },
    warningNumber() {
      return this.compilerOutput.then(function(r) {
        if (r.data.number > 1) {
          return "There are " + r.data.number + " warnings on this page.";
        } else if (r.data.number == 1) {
          return "There is 1 warning on this page.";
        } else {
          return "There are no warnings on this page.";
        }
      });
    },
    warningColour() {
      return this.compilerOutput.then(function(r) {
        return r.data.number > 0 ? "#fb9e9e" : "#9efb9e";
      });
    },
    warningShowMsg() {
      return this.warnings_visible ? "(Click to hide)" : "(Click to show)";
    },
    saveMessageVisible() {
      return this.saving ? "visible" : "hidden";
    },
    includeList() {
      return this.included_by;
    },
    includeOptions() {
      return this.included_by.length > 0;
    },
    duplicateWarning() {
      if (this.duplicated_by.length > 0) {
        var dup_text =
          "Note. You are editing the instructions for the following:";
        for (var i = 0; i < this.duplicated_by.length; i++) {
          dup_text += ' "' + this.duplicated_by[i] + '"';
        }
        return dup_text;
      } else {
        return "";
      }
    }
  },

  methods: {
    updateFromEditor(e) {
      this.editor_text = e.target.value;
    },

    updateFromServer() {
      this.$http.get("raw").then(function(response) {
        this.editor_text = response.body.md;
        this.editor_text_debounced = this.editor_text;
        this.saved_text = this.editor_text;
        this.page = response.body.page;
        this.duplicated_by = response.body.duplicated_by;
        this.included_by = response.body.included_by;
      });
    },

    updateInclude(event) {
      this.show_within =
        event.target.options[event.target.options.selectedIndex].value;
    },

    //This is instead of the debounce on `updateFromEditor` seems to stop the cursor jumping
    pollEditor() {
      setInterval(() => {
        this.editor_text_debounced = this.editor_text;
      }, 500);
    },

    saveChanges() {
      this.saving = true;
      this.$http
        .post("save", {
          md: this.editor_text,
          page: this.page,
          uploadedFiles: this.uploadedFiles
        })
        .then(response => {
          this.saving = false;
          if (response.body.saved) {
            this.saved_text = this.editor_text;
          } else {
            this.$fire({
              type: "error",
              text: "Save completed with error.",
              title: "Error",
              heightAuto: false
            });
          }
        })
        .catch(error => {
          this.saving = false;
          console.error(error);
          this.$fire({
            type: "error",
            text: "Save completed with error.",
            title: "Error",
            heightAuto: false
          });
        });
    },

    checkSave(e) {
      if (this.saved_text == this.editor_text) return;
      e.returnValue = `Are you sure you want to leave?`;
    },

    exitEditor() {
      window.location.href = window.location.href.replace(/\/-\/editor\//, "");
    },

    clearEditorSelection() {
      const textarea = this.$refs.edtext;
      textarea.selectionStart = textarea.selectionEnd;
    },

    editorHasTextSelected() {
      const textarea = this.$refs.edtext;
      return textarea.selectionStart !== textarea.selectionEnd;
    },

    insertEditorText(text) {
      const textarea = this.$refs.edtext;
      textarea.focus();
      document.execCommand("insertText", false, text);
    },

    wrapEditorText(beforeText, afterText) {
      const textarea = this.$refs.edtext;
      const currentValue = textarea.value;
      const selectionStart = textarea.selectionStart;
      const selectionEnd = textarea.selectionEnd;
      const selectedText = currentValue.slice(selectionStart, selectionEnd);
      textarea.focus();
      document.execCommand(
        "insertText",
        false,
        beforeText + selectedText + afterText
      );
    },

    addLink() {
      if (this.editorHasTextSelected()) {
        this.wrapEditorText("[", "](https://www.<Your Link Url>)");
      } else {
        this.insertEditorText("[Link Title](https://www.<Your Link Url>)");
      }
    },

    addFontStyle(fontStyleType) {
      const fontSyntax = fontStyleType === "italic" ? "_" : "**";
      if (this.editorHasTextSelected()) {
        this.wrapEditorText(fontSyntax, fontSyntax);
      } else {
        this.insertEditorText(fontSyntax + fontStyleType + fontSyntax);
      }
    },

    addBold() {
      this.addFontStyle("bold");
    },

    addItalic() {
      this.addFontStyle("italic");
    },

    addBom() {
      if (this.editorHasTextSelected()) {
        this.clearEditorSelection();
      }
      this.insertEditorText("\n{{BOM}}\n");
    },

    addPart() {
      if (this.editorHasTextSelected()) {
        this.wrapEditorText("[", "](partpage.md){qty: 1}");
      } else {
        this.insertEditorText("[PartName](partpage.md){qty: 1}");
      }
    },

    enable_dragbar() {
      this.$refs.cont.addEventListener("mousemove", this.bar_dragged);
      this.$refs.cont.addEventListener("mouseup", this.disable_dragbar);
    },

    disable_dragbar() {
      this.$refs.cont.removeEventListener("mouseup", this.disable_dragbar);
      this.$refs.cont.removeEventListener("mousemove", this.bar_dragged);
    },

    bar_dragged(e) {
      var pos = (100 * e.pageX) / this.$refs.cont.clientWidth;
      this.editor_width = pos - 0.5 + "%";
      this.preview_width = 100 - pos + "%";
    },

    hideshow_warnigns() {
      this.warnings_visible = !this.warnings_visible;
      if (this.warnings_visible) {
        this.preview_rows = "70% 24px auto";
      } else {
        this.preview_rows = "auto 24px 0";
      }
    },

    //Determines if the drag and drop functionality available
    determineDragAndDropCapable() {
      //Create a test element to see if certain events are present that let us do drag and drop.
      var div = document.createElement("div");
      return (
        ("draggable" in div || ("ondragstart" in div && "ondrop" in div)) &&
        "FormData" in window &&
        "FileReader" in window
      );
    },

    submitFiles() {
      let formData = new FormData();
      //loop over files
      for (var i = 0; i < this.files.length; i++) {
        let file = this.files[i];
        formData.append("files[" + i + "]", file);
      }
      this.files = [];
      //post files
      this.$axios
        .post("dropped-file", formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        })
        .then(
          function(response) {
            for (var i = 0; i < response.data.filenames.length; i++) {
              this.uploadedFiles.push(response.data.filenames[i]);
            }
            if (this.editorHasTextSelected()) {
              this.clearEditorSelection();
            }
            this.insertEditorText("\n" + response.data.md_line + "\n");
          }.bind(this)
        )
        .catch(error => {
          console.error(error);
          this.$fire({
            type: "error",
            text: "Upload completed with an error.",
            title: "Error",
            heightAuto: false
          });
        });
    }
  },

  created() {
    this.updateFromServer();
    this.pollEditor();
  }
};
</script>

